import React, { Component } from 'react';
import {Severity} from './Constants';
import './Notifier.css';
import { isNotificationError } from './NotificationUtils';
import SVG_info from '../resources/icons/notifier/info.svg';
import SVG_success from '../resources/icons/notifier/success.svg';
import SVG_warning from '../resources/icons/notifier/warning.svg';
import SVG_error from '../resources/icons/notifier/error.svg';
import SVG_close from '../resources/icons/notifier/close.svg';
import i18n from '../i18n';

class Notifier extends Component {

    constructor() {
        super();
        this.state = { 
          hidden: true
        }
        this.notification = {};
      }

    componentDidUpdate(prevProps) {
        if (prevProps.notification !== this.props.notification) {
            let hidden = (!this.props.notification.title && !this.props.notification.message);
            this.setState({ hidden });
            if (!hidden) this.notification = this.props.notification;
        }
    }
    
    onClose = () => {
        this.props.onClose({});
    };

    getSvgIcon(severity) {
        let svgIcon = SVG_info;
        switch(severity) {
            case Severity.INFO:
                break;
            case Severity.SUCCESS:
                svgIcon = SVG_success;
                break;   
            case Severity.WARNING:
                svgIcon = SVG_warning;
                break;
            case Severity.ERROR:
                svgIcon = SVG_error;
                break;
            default:
                break;
        }
        return svgIcon;
    }

    render() {
        const closable = (this.notification.closable === undefined ||  this.notification.closable);
        let hiddenClass = (this.state.hidden) ? ' notifier-hidden' : '';
        let severityClass = this.notification.severity || Severity.INFO;
        return (
            <>
                { (
                    <div className={'notifier ' + severityClass + hiddenClass}>
                        <img src={this.getSvgIcon(this.notification.severity)} alt="" />
                        {this.notification.title && (
                            <span className="notifier-title">{this.notification.title}:</span>
                        )}
                        {this.notification.message && (
                            <span className="notifier-message">{this.notification.message}</span>
                        )}
                        {closable && !isNotificationError(this.notification) && (
                            <div className="notifier-close">
                                <img src={SVG_close} alt={i18n.t('notifier.close')} onClick={this.onClose}/>
                            </div>
                        )}
                    </div>
                )}
            </>
        );
      }
}

export default Notifier;
