import React, { Component } from 'react';
import './Resizer.css';

const MouseMove = 'mousemove';
const MouseUp = 'mouseup';
const MinViewerWidth = 700;

class Resizer extends Component {

    constructor() {
        super();
        this.state = {
        }
        this.startResize = this.startResize.bind(this);
        this.resize = this.resize.bind(this);
        this.stopResize = this.stopResize.bind(this);
      }

    startResize() {
        if (this.props.target === null) return;
        
        document.body.style['user-select'] = 'none';
        window.addEventListener(MouseMove, this.resize, false);
        window.addEventListener(MouseUp, this.stopResize, false);
    }
    
    resize(e) {	
        if (e.buttons !== 1) this.stopResize();
        var w = window.document.documentElement.clientWidth - e.clientX;
        if (MinViewerWidth + w < window.document.documentElement.clientWidth) {
            this.props.target.style.width = w + 'px';
        }
    }
    
    stopResize() {
        document.body.style['user-select'] = '';
        window.removeEventListener(MouseMove, this.resize, false);
        window.removeEventListener(MouseUp, this.stopResize, false);
    }	
    
    autoSize() {
        this.props.target.style.width = 'min-content';
        this.props.target.style.width = this.props.target.clientWidth + 'px';
    }

    render() {
        return (
            <div className="resizer-container"
                onMouseDown = {e => { this.startResize(); }}
                onDoubleClick = {e => { this.autoSize(); }} >
                <div className="resizer-content" />
            </div>
        );
      }
}

export default Resizer;
