import React, { Component } from "react";
import "./DeviceViewer.css";
import axios from "axios";
import { MouseController, DoubleTapInterval } from "../actions/MouseController";
import {
  APIBaseUrl,
  BoundsUnit,
  Orientation,
  UIAction,
  HighlightTime,
  Severity,
} from "../utils/Constants";
import DeviceNavigationBar from "./DeviceNavigationBar";
import _ from "lodash";
import { createNotification } from "../utils/NotificationUtils";
import KeyboardControllerWebUI from "../actions/KeyboardControllerWebUI";
import KeyboardController from "../actions/KeyboardController";
import i18n from "../i18n";
import SVG_Blank from "../resources/images/blank.png";
import { Galleria } from "primereact/galleria";

class DeviceViewer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mobileScreenshot: {},
      snapshotData: null,
      snapshotError: false,
      ddtThumbnails: null,
      activeIndex: 0,
      screenshotId: null,
      actionResponse: null,
      isKeyboardActive: false
    };
    this.viewer = null;
    this.mouseController = null;
    this.selectedControlBounds = null;
    this.activeElementClass = null;
    this.mouseEventTimeouts = [];
    this.performAction = this.performAction.bind(this);
    this.itemsTemplate = this.itemsTemplate.bind(this);
    this.thumbnailTemplate = this.thumbnailTemplate.bind(this);
    this.onThumbnailChange = this.onThumbnailChange.bind(this);
    this.onItemChange = this.onItemChange.bind(this);
    this.keyboardComponent = React.createRef();
  }

  componentDidUpdate(prevProps) {
    if (this.props.sessionId !== prevProps.sessionId) {
      this.mouseController = new MouseController(this.props.sessionId, this.props.hasDesign);
      if (this.props.hasDesign === "false") {
        this.getMobileScreenshot(true);
      }
    }
    if (this.props.actionProcessed !== prevProps.actionProcessed) {
      if (this.props.hasDesign === "false") {
        this.getMobileScreenshot(
          this.props.refreshScreenshot !== prevProps.refreshScreenshot
        );
      }
    }
    if (
      this.props.actionProcessing !== prevProps.actionProcessing ||
      this.props.actionProcessed !== prevProps.actionProcessed
    ) {
      this.selectedControlBounds = null;
    }
  }

  getMobileScreenshot(refresh) {
    if (!this.props.sessionId) return;

    const headers = refresh
      ? { headers: { "If-Modified-Since": new Date().getTime() } }
      : null;
    const url = APIBaseUrl + this.props.sessionId + "/screenshot/";
    axios
      .get(url, headers)
      .then((response) => {
        if (response.data.screenshot) {
          window.setTimeout(
            () =>
              this.setState(
                {
                  snapshotData:
                    "data:image/png;base64, " + response.data.screenshot,
                  snapshotError: false,
                },
                () => this.props.onIncomingScreenshot(response.data.orientation)
              ),
            HighlightTime
          );
        }
      })
      .catch((error) => {
        let severity = Severity.ERROR;
        let message = i18n.t("dv.timeout");

        let r = error.response;
        if (r && r.data && r.data.status) {
          severity = Severity.WARNING;
          const errorId = "dv.error-" + r.data.status;
          message = i18n.t(errorId);
          if (message === errorId) {
            // Unknown status
            message = r.data.message;
          }
        }

        this.setState(
          {
            snapshotError: true,
            snapshotData: this.state.snapshotData || "",
          } // In case there is no previous screenshot
        );
        this.props.onNotification(createNotification("", message, severity));
      });
  }

  performAction(action, id) {
    let actionName = action.name;
    this.props.onInitAction(actionName);
    this.selectedControlBounds = null;
    action
      .perform(id)
      .then((response) => {
        this.activeElementClass = response.data.class;
        this.selectedControlBounds = response.data.coordinates;
        let recordedSteps =
          response.data === "" ? null : response.data.recordedsteps;
        this.props.onAction(actionName, recordedSteps);
        this.props.setProcessTestStep(false);
        this.setState({
          actionResponse : response.data
        })
      })
      .catch(() => {
        this.props.onAction(
          actionName,
          null,
          createNotification(
            actionName,
            i18n.t("dv.error-action"),
            Severity.WARNING
          )
        );
        this.props.setProcessTestStep(false);
      });
  }

  setKeyboardActiveStatus = (status) => {
    this.setState({
      isKeyboardActive : status
    })
  }

  onMouseEvent(event, id) {
    event.preventDefault();
    let action = this.mouseController.handleEvent(event, this.viewer);
    if (action !== null) {
      this.props.setProcessTestStep(true);
      if (action.name === UIAction.DOUBLETAP.id) {
        this.mouseEventTimeouts.forEach((t) => {
          window.clearTimeout(t);
        });
      }
      if (action.name === UIAction.TAP.id) {
        let tap = _.cloneDeep(action);
        this.mouseEventTimeouts.push(
          window.setTimeout(() => this.performAction(tap), DoubleTapInterval)
        );
      } else {
        if(this.state.isKeyboardActive) {
          this.keyboardComponent.current.recordStep();
          window.setTimeout(() => {
            this.performAction(action, id);
            this.setKeyboardActiveStatus(false);
          }, 500);
        } else {
          this.performAction(action, id);
        }
      }
    }
  }

  getSelectedControlBounds() {
    if (!this.viewer || !this.selectedControlBounds) return {};

    let zoomFactor = this.viewer.naturalHeight / this.viewer.clientHeight;
    let bounds = {};
    this.selectedControlBounds
      .split(";")
      .filter((b) => b !== "")
      .forEach((b) => {
        let bound = b.split(":");
        bounds[bound[0]] = parseInt(bound[1]) / zoomFactor;
      });

    bounds.width = bounds.right - bounds.left + BoundsUnit;
    bounds.height = bounds.bottom - bounds.top + BoundsUnit;
    bounds.left = bounds.left + BoundsUnit;
    bounds.top = bounds.top + BoundsUnit;
    delete bounds.right;
    delete bounds.bottom;

    return bounds;
  }

  itemsTemplate(item) {
    return (
      <>
      <img
        src={item.screenshot}
        alt={item.id}
        ref={(el) => (this.viewer = el)}
        draggable="false"
        onMouseDown={(e) => {
          this.onMouseEvent(e, item.id);
        }}
        className="max-image"
      />

      <KeyboardControllerWebUI
        sessionId={this.props.sessionId}
        platformName={this.props.platformName}
        activeElementClass={this.activeElementClass}
        screenshotId={item.id}
        performAction={this.performAction}
        actionResponse = {this.state.actionResponse}
        setProcessTestStep = {this.props.setProcessTestStep}
        viewer = {this.viewer}
        thumbnailIndex = {this.state.activeIndex}
        setKeyboardActiveStatus = {this.setKeyboardActiveStatus}
        ref={this.keyboardComponent}
      />
      </>
    );
  }

  thumbnailTemplate(item) {
    return (
      <img src={item.screenshot} alt={item.id} className="thumbnail-images" />
    );
  }

  renderFooter() {
    return (
      <div className="custom-galleria-footer">
        {this.state.ddtThumbnails && (
          <span className="title-container">
            <span>
              {this.state.activeIndex + 1}/{this.state.ddtThumbnails.length}
            </span>
            <span className="title">
              {this.state.ddtThumbnails[this.state.activeIndex].title}
            </span>
            <span>{this.state.ddtThumbnails[this.state.activeIndex].alt}</span>
          </span>
        )}
      </div>
    );
  }

  onItemChange(event) {
    this.setState({ activeIndex: event.index });
  }

  onThumbnailChange(event) {
    this.setState({ activeIndex: event.index });
  }

  render() {
    const footer = this.renderFooter();
    if (this.state.snapshotData === null && this.props.snapShotData.length === 0) {
      return (
        <>
          <div className="dv-pb-text">{i18n.t("dv.initializing")}</div>
          <div className="dv-pb-container">
            <div className="dv-pb-content" />
          </div>
        </>
      );
    }

    const snapshotData = this.state.snapshotData || SVG_Blank;
    let processingClass = (this.props.actionProcessing) ? ' dv-processing' : '';
    let selectedControlVisible = (!this.mouseController.isSwipe());
    let orientationClass = (this.props.orientation === Orientation.LANDSCAPE) ? ' dv-landscape' : ' dv-portrait';
    let visibleClass = (this.props.visibility) ? '' : ' dv-hidden';
    let disableClass = (this.state.snapshotError) ? ' dv-disabled' : '';

    return (
      <>
        {this.props.hasDesign === "false" ? (
          <div className="dv">
            <div
              className={
                "dv-content" +
                orientationClass +
                processingClass +
                visibleClass +
                disableClass
              }
            >
              <img
                ref={(el) => (this.viewer = el)}
                alt={i18n.t("dv.title")}
                src={snapshotData}
                draggable="false"
                onMouseDown={(e) => {
                  this.onMouseEvent(e);
                }}
                onMouseUp={(e) => {
                  this.onMouseEvent(e);
                }}
                onMouseLeave={(e) => {
                  this.onMouseEvent(e);
                }}
                onDoubleClick={(e) => {
                  this.onMouseEvent(e);
                }}
              />

              {selectedControlVisible && (
                <div
                  className="dv-selected-control"
                  style={this.getSelectedControlBounds()}
                />
              )}
            </div>

            <DeviceNavigationBar
              sessionId={this.props.sessionId}
              platformName={this.props.platformName}
              onInitAction={this.props.onInitAction}
              onAction={this.props.onAction}
            />

            <KeyboardController
              sessionId={this.props.sessionId}
              platformName={this.props.platformName}
              activeElementClass={this.activeElementClass}
              performAction={this.performAction}
            />
          </div>
        ) : (
          <div className="thumbnails">
            <div className="card dv-web">
              <Galleria
                value={this.props.snapShotData}
                numVisible={8}
                item={this.itemsTemplate}
                thumbnail={this.thumbnailTemplate}
                style={{ maxWidth: "100%" }}
                activeIndex={this.state.activeIndex}
                onItemChange={this.onItemChange}
                footer={footer}
                pt={{
                  previousThumbnailButton: { "aria-label" : "previous image button"},
                  nextThumbnailButton: { "aria-label" : "next image button"}
                }}
              />
            </div>
          </div>
        )}
      </>
    );
  }
}

export default DeviceViewer;
