import React, { Component } from 'react';
import './DeviceNavigationBar.css';
import {HWAction, PlatformName} from '../utils/Constants';
import {postHWSteps} from '../utils/APIUtils';
import {DoubleTapInterval} from "../actions/MouseController";
import i18n from '../i18n';
import SVG_Overview from '../resources/icons/overview.svg';
import SVG_Home from '../resources/icons/home.svg';
import SVG_Back from '../resources/icons/back.svg';

class DeviceNavigationBar extends Component {

  constructor() {
    super();
    this.homeTimeoutId = null;
    this.isAndroid = null;
  }

  componentDidMount() {
    this.isAndroid = (this.props.platformName === PlatformName.ANDROID);
  }

  onOverview = () => {
    window.clearTimeout(this.homeTimeoutId);
    this.performAction(HWAction.OVERVIEW);
  };

  onHome = () => {
    if (this.isAndroid) {
      this.performAction(HWAction.HOME);
    }
    else {
      // on iOS, perform the action only if a double tap is not received
      window.clearTimeout(this.homeTimeoutId);
      this.homeTimeoutId = window.setTimeout(() => {
        this.performAction(HWAction.HOME)
      }, DoubleTapInterval);
    }
  };

  onBack = () => {
    this.performAction(HWAction.BACK);
  };

  performAction(action, actionArgs) {
    this.props.onInitAction(action.id);
    postHWSteps(this.props.sessionId, action.id, this.props.onAction, actionArgs);
  }

  render() {
    let homeDblClickHandler = (this.isAndroid) ? null : this.onOverview;
    return (
      <div className="dnb">
        {this.isAndroid && <img
          src={SVG_Overview} 
          alt={i18n.t('dnb.overview')}
          onClick={this.onOverview} />}
        <img
          src={SVG_Home}
          alt={i18n.t('dnb.home')}
          onClick={this.onHome}
          onDoubleClick={homeDblClickHandler} />
        {this.isAndroid && <img
          src={SVG_Back}
          alt={i18n.t('dnb.back')}
          onClick={this.onBack} />}
      </div>
    );
  }
}

export default DeviceNavigationBar;
