import { MouseAction } from "./MouseAction";
import { MouseEventType, UIAction } from "../utils/Constants";

const SwipeHorizontalMinDistance = 10;
const SwipeVerticalMinDistance = 10;
const TapMaxDistance = 10;
const TapMaxTime = 500;
export const DoubleTapInterval = 300;

export class MouseController {
  constructor(sessionId, hasDesign) {
    this.action = new MouseAction(sessionId);
    this.zoomFactor = 1;
    this.hasDesign = hasDesign;
  }

  isSwipe() {
    return (Math.abs(this.action.endx - this.action.startx) > SwipeHorizontalMinDistance * this.zoomFactor
      || Math.abs(this.action.endy - this.action.starty) > SwipeVerticalMinDistance * this.zoomFactor);
  }

  isTap() {
    return (Math.abs(this.action.endx - this.action.startx) < TapMaxDistance * this.zoomFactor
      && Math.abs(this.action.endy - this.action.starty) < TapMaxDistance) * this.zoomFactor;
  }

  isLongTap() {
    return (new Date().getTime() - this.action.startTime > TapMaxTime);
  }

  handleEvent(event, target) {
    if (event.button !== 0) return null;

    this.action.name = null;
    this.zoomFactor = (target.naturalHeight / target.clientHeight);
    let rect = target.getBoundingClientRect();

    // Image relative coordinates
    let x = Math.round((event.clientX - rect.left) * this.zoomFactor);
    let y = Math.round((event.clientY - rect.top) * this.zoomFactor);
    if(this.hasDesign === 'false' || this.hasDesign === undefined) {
    switch(event.type) {
      case MouseEventType.DOWN:
        this.action.setStartPosition(x, y);
        this.action.startTime = new Date().getTime();
        this.action.buttonPressed = true;
        break;
      case MouseEventType.UP:
        if (this.action.buttonPressed) {
          this.action.setEndPosition(x, y);
          if (this.isSwipe()) {
            this.action.name = UIAction.SCROLL.id;
          }
          else {
            if (this.isTap()) {
              this.action.name = (this.isLongTap()) ? UIAction.LONGTAP.id : UIAction.TAP.id;
            }
          }
        }
        this.action.buttonPressed = false;
        break;
      case MouseEventType.LEAVE:
        if (this.action.buttonPressed) {
            this.action.setEndPosition(Math.max(0, Math.min(x, target.naturalWidth - 1)),
              Math.max(0, Math.min(y, target.naturalHeight - 1)));
          if (this.isSwipe()) {
            this.action.name = UIAction.SCROLL.id;
          }            
        }
        this.action.buttonPressed = false;
        break;
      case MouseEventType.DBLCLICK:
        this.action.setEndPosition(x, y);
        this.action.name = UIAction.DOUBLETAP.id;
        break;
      default:
        break;
    } } else { 
      this.action.setStartPosition(x, y);
      this.action.name = "onclick";
    }

    if (this.action.name === null) return null;
    
    return this.action;
  }
}

export default MouseController;