import React, { Component } from 'react';
import './Dialer.css';
import {OverlayPanel} from 'primereact/overlaypanel';
import {InputText} from 'primereact/inputtext';
import {InputTextarea} from 'primereact/inputtextarea';
import {Button} from 'primereact/button';
import i18n from '../i18n';

class Dialer extends Component {

    constructor() {
        super();
        this.state = {
            phoneNumber: '',
            message: ''
        }
        this.op = null;
    }
    
    componentDidUpdate(prevProps) {
        if (prevProps.toggle !== this.props.toggle) {
            this.op.toggle(this.props.toggle.event);
        }
    }

    dial = () => {
        this.props.onDial(this.props.toggle.isSMS, this.state.phoneNumber, this.state.message);
        this.op.hide();
    }

    onHide = () => {
        this.setState({ message: '' });
    }

    canDial() {
        return (this.state.phoneNumber !== '')
            && ((this.props.toggle.isSMS) ? this.state.message !== '' : true);
    }

    getTarget() {
        let target = this.props.toggle.event.target;
        return (target.className !== 'tb-button') ? target.parentElement : target;
    }

    render() {
        const btnLabel = (this.props.toggle.isSMS) ? i18n.t('dialer.receive') : i18n.t('dialer.call');
        return (
            <>
            {this.props.toggle.event && (
                <OverlayPanel className="dialer" ref={el => this.op = el} onHide={this.onHide} appendTo={this.getTarget()}>
                    <div>
                        <div className="dialer-field">
                            <label className="required" for="from">{i18n.t('dialer.from')}</label>
                            <InputText
                                id="from"
                                keyfilter={new RegExp(/[\d\-.+]/)}
                                value={this.state.phoneNumber}
                                onChange={e => this.setState({ phoneNumber: e.target.value })}
                            />
                        </div>
                        {this.props.toggle.isSMS && (<div className="dialer-field">
                            <label className="required" for="message">{i18n.t('dialer.message')}</label>
                            <InputTextarea
                                id="message"
                                rows={4}
                                value={this.state.message}
                                onChange={e => this.setState({ message: e.target.value })}
                            />
                        </div>)}
                    </div>
                    <div className="dialer-button-bar">
                        <Button
                            label={btnLabel}
                            onClick={this.dial}
                            disabled={!this.canDial()}
                        />
                    </div>
                </OverlayPanel>
            )}
            </>
        );
      }
}

export default Dialer;
