import React, { Component } from 'react';
import { EditClass } from '../utils/Constants';
import { KeyboardAction } from './KeyboardAction';
import i18n from '../i18n';

const specialCharDelimiter = '|';
const SpecialChar = [
  {keyCode: 8, name: '[BACK_SPACE]', ANDROID: true, IOS: true},
  {keyCode: 37, name: '[ARROW_LEFT]', ANDROID: true, IOS: false},
  {keyCode: 38, name: '[ARROW_UP]', ANDROID: true, IOS: false},
  {keyCode: 39, name: '[ARROW_RIGHT]', ANDROID: true, IOS: false},
  {keyCode: 40, name: '[ARROW_DOWN]', ANDROID: true, IOS: false},
  {keyCode: 46, name: '[DELETE]', ANDROID: true, IOS: false}
]

class KeyboardController extends Component {

  constructor() {
      super();
      this.state = { 
        textContent: '',
        textareaStyles: null
      }
      this.action = null;
      this.keyboardListener = null;
      this.keyboardTimeout = null;
      this.specialChars = '';
      this.listenerFunction = this.listenerFunction.bind(this);
  }

  componentDidMount() {
    this.action = new KeyboardAction(this.props.sessionId);

    document.addEventListener('keydown', (e) => {
      if (!(e.target instanceof HTMLInputElement)
        && !(e.target instanceof HTMLTextAreaElement)) {
          if (EditClass.includes(this.props.activeElementClass)) {
            this.keyboardListener.focus();
          }
      }
    });
    document.addEventListener("keydown", this.listenerFunction, false);
  }

  componentWillUnmount(){
    document.removeEventListener("keydown", this.listenerFunction, false);
  }

  componentDidUpdate(prevProps) {
    if(prevProps.thumbnailIndex !== this.props.thumbnailIndex) {
      let styles = {left : 0, top : 0, opacity: 0, border: 'none'};
      this.setState({
          textareaStyles: styles
      })
    } 
    if(this.props.actionResponse !== null) {
        if (this.props.actionResponse !== prevProps.actionResponse) {
            if(this.props.actionResponse.class === "Edit") {
                let zoomFactor = this.props.viewer.naturalHeight / this.props.viewer.clientHeight;
                let coordinates = this.props.actionResponse.coordinates;
                let coordinatesStyle = coordinates.split(";");
                let leftStyle = coordinatesStyle[0].split(":")[1] / zoomFactor;
                let topStyle = coordinatesStyle[1].split(":")[1] / zoomFactor;
                let bottomStyle = coordinatesStyle[3].split(":")[1];
                let width = Math.round(bottomStyle);
                let height = bottomStyle - topStyle;
                leftStyle = Math.round(leftStyle);
                if(this.state.textContent === '' ) {
                  this.props.setKeyboardActiveStatus(true);
                  let styles = {left : leftStyle, top : topStyle, width : width, height : height, opacity: 1, border: '1px solid black'};
                  this.setState({
                    textareaStyles: styles
                  })
                } else {
                  let styles = {left : 0, top : 0, opacity: 0, border: 'none'};
                  this.setState({
                      textareaStyles: styles,
                      textContent : ""
                  })
                }
            } else {
                let styles = {left : 0, top : 0, opacity: 0, border: 'none'};
                this.setState({
                    textareaStyles: styles
                })
            }
        }
    }
  }

  listenerFunction(event){
    if (event.key === "Escape") {
      this.setState({ textContent: '' });
      let styles = {left : 0, top : 0, opacity: 0, border: 'none'};
      this.setState({
          textareaStyles: styles
      })
    }
    if (event.key === "Enter") { 
      const text = this.state.textContent
        || (this.specialChars && specialCharDelimiter + this.specialChars + specialCharDelimiter);
      if (text) {
        this.props.setProcessTestStep(true);
        this.action.value = text;
        this.props.performAction(this.action, this.props.screenshotId);
        let styles = {left : 0, top : 0, opacity: 0, border: 'none'};
        this.setState({
            textareaStyles: styles
        })
      }
    }
  }


  recordStep = () => {
      const text = this.state.textContent
        || (this.specialChars && specialCharDelimiter + this.specialChars + specialCharDelimiter);
      if (text) {
        this.props.setProcessTestStep(true);
        this.action.value = text;
        this.props.performAction(this.action, this.props.screenshotId);
        let styles = {left : 0, top : 0, opacity: 0, border: 'none'};
        this.setState({
            textareaStyles: styles
        })
      }
  }

  render() {
    let styles = this.state.textareaStyles;
    return (
        <>
          <label htmlFor="keyboardTextarea"></label>
          <textarea
            id="keyboardTextarea"
            ref={el => this.keyboardListener = el}
            value={this.state.textContent}
            onChange={e => this.setState({ textContent: e.target.value })}
            style={{left: styles !== null ? styles.left : 0, top: styles !== null ? styles.top : 0, 
              opacity: styles !== null ? styles.opacity : 0,
              border: styles !== null ? styles.border : "none"}}
          />
        </>
    );
  }
}

export default KeyboardController;
