import React, { Component } from "react";
import "./DeviceToolBar.css";
import { HWAction, Orientation, PlatformName } from "../utils/Constants";
import { postHWSteps } from "../utils/APIUtils";
import Dialer from "../utils/Dialer";
import i18n from "../i18n";
import SVG_Stop from "../resources/icons/stop.svg";
import SVG_Stop_Disabled from "../resources/icons/stop-disabled.svg";
import SVG_Lock from "../resources/icons/screen-lock.svg";
import SVG_Unlock from "../resources/icons/screen-unlock.svg";
import SVG_Refresh from "../resources/icons/refresh.svg";
import SVG_Portrait from "../resources/icons/rotate-portrait.svg";
import SVG_Landscape from "../resources/icons/rotate-landscape.svg";
import SVG_Volume_Up from "../resources/icons/volume-up.svg";
import SVG_Volume_Down from "../resources/icons/volume-down.svg";
import SVG_Volume_Mute from "../resources/icons/volume-mute.svg";
import SVG_Sms from "../resources/icons/receive-sms.svg";
import SVG_Call from "../resources/icons/receive-call.svg";

export class DeviceToolBar extends Component {
  constructor() {
    super();
    this.state = {
      screenLocked: false,
      dialerToggle: { event: null, isSMS: false },
    };
  }

  onCloseSession = () => {
    this.props.onInitAction(HWAction.CLOSE.id);
    this.performAction(HWAction.CLOSE);
    window.removeEventListener("unload", this.props.closeListener, false);
    window.close();
  };

  onRefreshScreenshot = () => {
    let actionName = HWAction.REFRESH.id;
    this.props.onInitAction(actionName);
    this.props.onAction(actionName);
  };

  toggleRotate = () => {
    this.performAction(
      this.props.orientation === Orientation.PORTRAIT
        ? HWAction.ROTATELANDSCAPE
        : HWAction.ROTATEPORTRAIT
    );
  };

  toggleScreenLock = () => {
    this.performAction(
      this.state.screenLocked ? HWAction.SCREENUNLOCK : HWAction.SCREENLOCK
    );
    window.setTimeout(
      () =>
        this.setState({
          screenLocked: !this.state.screenLocked,
        }),
      2000
    ); // TODO
  };

  onVolumeUp = () => {
    this.performAction(HWAction.VOLUMEUP);
  };

  onVolumeDown = () => {
    this.performAction(HWAction.VOLUMEDOWN);
  };

  onVolumeMute = () => {
    this.performAction(HWAction.VOLUMEMUTE);
  };

  onSendMessage = (e) => {
    e.persist();
    this.setState({ dialerToggle: { event: e, isSMS: true } });
  };

  onMakeCall = (e) => {
    e.persist();
    this.setState({ dialerToggle: { event: e, isSMS: false } });
  };

  performAction(action, actionArgs) {
    this.props.onInitAction(action.id);
    postHWSteps(
      this.props.sessionId,
      action.id,
      this.props.onAction,
      actionArgs
    );
  }

  onDialPerformed = (isSMS, phoneNumber, message) => {
    if (isSMS) {
      this.performAction(
        HWAction.RECEIVESMSNOTIFICATON,
        JSON.stringify({ phoneNumber, message })
      );
    } else {
      this.performAction(HWAction.RECEIVECALL, JSON.stringify({ phoneNumber }));
    }
  };

  renderButton(icon, handler, tooltip, disabled) {
    let btnClassName = "tb-button";
    if (disabled) {
      btnClassName += " tb-button-disabled";
      tooltip = i18n.t("dtb.no-support");
    }
    return (
      <span className={btnClassName} onClick={handler} title={tooltip} tabIndex={0} 
      onKeyDown={e => this.props.onKeyPressForAccessible(e)}>
        <img src={icon} alt={tooltip} />
      </span>
    );
  }

  render() {
    let isAndroid = this.props.platformName === PlatformName.ANDROID;
    let isIOSSimulator =
      !isAndroid && this.props.deviceName.startsWith("Simulator");
    let disabledClass = this.props.sessionId === null ? " tb-disabled" : "";
    let platformClass = isAndroid ? "" : " tb-content-ios";
    let stopIcon = this.props.sessionId !== null ? SVG_Stop : SVG_Stop_Disabled;
    let rotateIcon =
      this.props.orientation === Orientation.PORTRAIT
        ? SVG_Landscape
        : SVG_Portrait;
    let lockIcon = this.state.screenLocked ? SVG_Unlock : SVG_Lock;
    let mobileIcons =
      this.props.hasDesign === "false" ? (
        <>
          <div className="tb-separator" />
          {this.renderButton(
            lockIcon,
            this.toggleScreenLock,
            i18n.t("dtb.lock")
          )}
          {this.renderButton(
            SVG_Refresh,
            this.onRefreshScreenshot,
            i18n.t("dtb.refresh")
          )}
          {this.renderButton(
            rotateIcon,
            this.toggleRotate,
            i18n.t("dtb.rotate")
          )}
          <div className="tb-separator" />
          {this.renderButton(
            SVG_Volume_Up,
            this.onVolumeUp,
            i18n.t("dtb.volume-up"),
            isIOSSimulator
          )}

          {this.renderButton(
            SVG_Volume_Down,
            this.onVolumeDown,
            i18n.t("dtb.volume-down"),
            isIOSSimulator
          )}

          {isAndroid && (
            <>
              {this.renderButton(
                SVG_Volume_Mute,

                this.onVolumeMute,

                i18n.t("dtb.volume-mute")
              )}
              <div className="tb-separator" />
              {this.renderButton(
                SVG_Sms,
                this.onSendMessage,
                i18n.t("dtb.message")
              )}

              {this.renderButton(
                SVG_Call,
                this.onMakeCall,
                i18n.t("dtb.call")
              )}
            </>
          )}

          <div className="tb-separator" />
        </>
      ) : (
        ""
      );
    return (
      <div className={"tb" + disabledClass}>
        <div className={"tb-content" + disabledClass + platformClass}>
          {this.renderButton(stopIcon, this.onCloseSession, i18n.t("dtb.stop"))}
          <>{mobileIcons}</>
        </div>

        <Dialer
          toggle={this.state.dialerToggle}
          onDial={this.onDialPerformed}
        />
      </div>
    );
  }
}

export default DeviceToolBar;
