import {ActionType, UIAction} from '../utils/Constants';
import {postSteps} from '../utils/APIUtils';

export class KeyboardAction {
    constructor(sessionId) {
      this.sessionId = sessionId;
      this.name = UIAction.ENTERTEXT.id;
      this.value = '';
    }

    buildActionArgs() {
      var actionargs = {
        value: this.value
      }

      return JSON.stringify(actionargs);
    }

    perform(screenshotId) {
      if(screenshotId === null || screenshotId === undefined) {
        return postSteps(this.sessionId, ActionType.UI, this.name, this.buildActionArgs());
      } else {
        return postSteps(this.sessionId, ActionType.UI, this.name, this.buildActionArgs(), screenshotId);
      }
    }
}