import React, { Component } from 'react';
import './DeviceTestSteps.css';
import Resizer from '../utils/Resizer';
import {HWAction, UIAction, InputAction} from '../utils/Constants';
import SVG_processing from '../resources/icons/processing.svg';
import SVG_uiaction from '../resources/icons/ui-action.svg';
import i18n from '../i18n';
import SVG_Left from '../resources/icons/chevron-left.svg';
import SVG_Right from '../resources/icons/chevron-right.svg';

class DeviceTestSteps extends Component {

  constructor() {
    super();
    this.state = {
      steps: [],
      minimized: false
    }
    this.stepsContainer = null;
    this.stepsContent = null;
    this.actionNames = {};
  }

  componentDidMount() {
    // Build a map event/label to speed up label generation
    const actions = {...HWAction, ...UIAction};
    for (const a in actions) {
      this.actionNames[actions[a].id] = actions[a].label;
    }
  }

  componentDidUpdate(prevProps) {
    if (this.props.recordedSteps && (prevProps.recordedSteps !== this.props.recordedSteps)) {
      let steps = (this.isTextInputRunning()) 
        ? this.state.steps.slice(0, this.state.steps.length - 1)
        : this.state.steps;
      this.setState({ steps: [...steps, ...this.props.recordedSteps]});
    }
    this.scrollToBottom();
  }

  isTextInputRunning() {
    let sSteps = this.state.steps;
    let pSteps = this.props.recordedSteps;
    return sSteps.length > 0 && InputAction.includes(sSteps[sSteps.length - 1].actiontype)
      && pSteps.length > 0 && InputAction.includes(pSteps[pSteps.length - 1].actiontype);
  }
  
  scrollToBottom() {
    if (this.stepsContent !== null) {
      this.stepsContent.scrollTop = this.stepsContent.scrollHeight;
    }
  }

  toggleMinimized = () => {
    this.setState({ minimized: !this.state.minimized });
    this.props.onMinimize(!this.state.minimized);
  };

  addStep(svgName, label) {
    let processingClass = (svgName === SVG_processing) ? ' dts-processing' : '';
    return (
      <div className={'dts-step' + processingClass} title={label}>
        <span className="dts-step-icon"><img src={svgName} alt={svgName} /></span>
        {!this.state.minimized && <span className="dts-step-label">{label}</span>}
      </div>
    );
  }

  getSvgActionIcon(type) {
    for (var key in HWAction) {
      if (HWAction[key].id === type) {
        let icon = HWAction[key].icon;
        if (icon !== null) return icon;
      }
    }
    return SVG_uiaction; // Default
  }

  getStepsLabel(step) {
    let label = this.actionNames[step.actiontype];
    const details = (step.controlDetails) ? JSON.parse(step.controlDetails): {};
    
    // convert role to control name
    details.control = i18n.t('controls.' + details.role);

    return i18n.t(label, details) || step.actiontype;
  }

  render() {
    let minimizedClass = (this.state.minimized) ? ' dts-minimized' : '';
    let headerIcon = (this.state.minimized) ? SVG_Left : SVG_Right;
    return (
      <div className="dts">
        <div className={'dts-container' + minimizedClass} ref={el => this.stepsContainer = el}>
          <div className="dts-header">
            {!this.state.minimized && <span>{i18n.t('dts.title')}</span>}
            <img src={headerIcon} alt="" onClick={this.toggleMinimized} tabIndex={0} onKeyDown={e => this.props.onKeyPressForAccessible(e)}/>
          </div>
          
          <div className="dts-content">
            {!this.state.minimized && <Resizer target={this.stepsContainer} />}
            <div className={'dts-steps' + minimizedClass} ref={el => this.stepsContent = el}>

              {this.props.sessionId && (
                <>
                  {this.state.steps.map((step, index) => {
                    const stepLabel = this.getStepsLabel(step);
                    return stepLabel && <div key={index}>
                      {index !== 0 && <div className="dts-separator" />}
                      {this.addStep(this.getSvgActionIcon(step.actiontype), stepLabel)}
                    </div>
                  })}
                </>
              )}
              {
                this.props.processTestStep && (
                  <div style={{marginTop: 6, marginLeft: 11}}>{i18n.t('dts.processing')}</div>
                )
              }
              {this.props.actionProcessing && (
                <span>
                  {this.props.sessionId && (
                    <div className="dts-separator" />
                  )}
                  {this.addStep(SVG_processing, i18n.t('dts.processing'))}
                </span>
              )}

              <div className="dts-steps-footer" />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default DeviceTestSteps;
