import {ActionType, UIAction} from '../utils/Constants';
import {postSteps} from '../utils/APIUtils';

export class MouseAction {
    constructor(sessionId) {
      this.sessionId = sessionId;
      this.name = null;
      this.startx = 0;
      this.starty = 0;
      this.endx = 0;
      this.endy = 0;
      this.startTime = 0;
      this.buttonPressed = false;
    }

    setStartPosition(x, y) {
      this.startx = x;
      this.starty = y;
    }

    setEndPosition(x, y) {
      this.endx = x;
      this.endy = y;
    }

    buildActionArgs() {
      var actionargs = {
        x: this.endx,
        y: this.endy
      }

      if (this.name === UIAction.SCROLL.id) {
        actionargs = {
          "sx": this.startx,
          "sy": this.starty,
          "dx": this.endx, 
          "dy": this.endy
        }
      }

      if (this.name === "onclick") {
        actionargs = {
          "x": this.startx,
          "y": this.starty,
        }
      }

      return JSON.stringify(actionargs);
    }

    perform(screenshotId) {
      if(screenshotId === null || screenshotId === undefined) {
        return postSteps(this.sessionId, ActionType.UI, this.name, this.buildActionArgs());
      } else {
        return postSteps(this.sessionId, ActionType.UI, this.name, this.buildActionArgs(), screenshotId);
      }
      
    }
}