import axios from "axios";
import {APIBaseUrl, ActionType, Severity} from './Constants';
import { createNotification } from "./NotificationUtils";
import i18n from '../i18n';

export const postSteps = (sessionId, actiontype, actionName, actionArgs, screenshotId) => {
    const url = APIBaseUrl + sessionId + "/steps/";
    if(screenshotId === null || screenshotId === undefined) {
      screenshotId = new Date().getTime()
    }
    return axios.post(url, {
        "actiontype": actiontype,
        "action": actionName,
        "actionargs": actionArgs,
        "screenshotid": screenshotId
      });
}

export const postHWSteps = (sessionId, actionName, handler, actionArgs) => {
  postSteps(sessionId, ActionType.HW, actionName, actionArgs)
    .then(response => {
      let recordedSteps = (response.data === '') ? null : response.data.recordedsteps;
      handler(actionName, recordedSteps);
    })
    .catch(() => {
      handler(
        actionName,
        null,
        createNotification(actionName, i18n.t('dv.error-action'), Severity.WARNING)
      );
  });
}

export const closeSession = (sessionId, actionName, handler) => {
  if (sessionId) {
    const url = APIBaseUrl + sessionId;
    axios.delete(url)
      .then(() => {
        if (handler) handler(actionName);
      })
      .catch();
  }
}
