import React, { Component } from "react";
import axios from "axios";
import {
  HomeApp,
  APIBaseUrl,
  HWAction,
  Orientation,
  Severity,
  PlatformName,
  DeviceType,
} from "./utils/Constants";
import "./VirtualClient.css";
import DeviceViewer from "./components/DeviceViewer";
import { DeviceToolBar } from "./components/DeviceToolBar";
import DeviceTestSteps from "./components/DeviceTestSteps";
import Notifier from "./utils/Notifier";
import {
  createNotification,
  isNotificationError,
} from "./utils/NotificationUtils";
import { closeSession } from "./utils/APIUtils";
import Feedback from "./utils/Feedback";
import i18n from "./i18n";

class VirtualClient extends Component {
  constructor() {
    super();
    this.state = {
      sessionId: null,
      initialised: false,
      notification: {},
      recordedSteps: null,
      actionProcessing: false,
      actionProcessed: false,
      sessionClosed: false,
      refreshScreenshot: false,
      orientation: Orientation.PORTRAIT,
      viewerVisibility: true,
      hasDesign: false,
      snapshotIds: [],
      snapShotData: [],
      processTestStep: false
    };
    this.appPackage = null;
    this.appActivity = null;
    this.iOSBundleId = null;
    this.orgId = null;
    this.deviceName = "";
    this.deviceId = null;
    this.closeListener = null;
    this.statusIntervalId = null;
    this.platformName = null;
    this.platformVersion = null;
    this.screenlockTimeoutId = null;
    this.appiumTimeoutId = null;
    this.appiumIntervalId = null;
    this.appiumUrl = null;
  }

  componentDidMount() {
    let success = false;
    const params = new URLSearchParams(window.location.search);
    const appUid = params.get("appUid");
    this.appPackage = params.get("appPackage") || HomeApp;
    this.appActivity = params.get("appActivity") || HomeApp;
    this.iOSBundleId = params.get("iOSBundleId") || HomeApp;
    this.orgId = params.get("orgId");
    this.deviceName = params.get("deviceName") || "";
    this.deviceId = params.get("deviceId");
    this.platformName = params.get("platformName");
    this.platformVersion = params.get("platformVersion");
    this.appiumUrl = params.get("appiumUrl");
    this.hasDesign = params.get("hasdesigns") || "false";
    const sessionId = params.get("sessionId");
    //const sessionId = Math.random();
    this.setState({
      hasDesign: this.hasDesign,
    });
    if (this.hasDesign === "false") {
      if (appUid !== null && sessionId !== null) {
        this.setState({ actionProcessing: true });
        axios
          .post(APIBaseUrl, {
            sessionid: sessionId,
            type: "Android",
            VERSION: this.platformVersion,
            deviceName: this.deviceName,
            deviceId: this.deviceId,
            platformName: this.platformName,
            appPackage: this.appPackage,
            appActivity: this.appActivity,
            iOSBundleId: this.iOSBundleId,
            orgId: this.orgId,
            appiumURL: this.appiumUrl,
            appconfigurationid: appUid,
          })
          .then((response) => {
            let d = response.data;
            console.log(
              i18n.t("vc.launch", {
                appname:
                  this.platformName === PlatformName.ANDROID
                    ? this.appPackage
                    : this.iOSBundleId,
                device: d.devicename,
              })
            );
            success = true;
            this.setState({
              sessionId: d.sessionid,
              recordedSteps: d.recordedsteps,
              actionProcessing: false,
              notification: createNotification(
                "",
                i18n.t("vc.info-launch"),
                Severity.INFO
              ),
            });
          })
          .catch((error) => {
            this.getDeviceDisplayName(this.deviceName);
            let r = error.response;
            if (r && r.status === 409 && r.data) {
              success = true;
              this.restoreSession(sessionId, r.data);
            } else {
              let name =
                this.platformName === PlatformName.ANDROID
                  ? this.appPackage
                  : this.iOSBundleId;
              this.setState({
                notification: createNotification(
                  "",
                  i18n.t("vc.error-launch", { name }),
                  Severity.ERROR
                ),
              });
            }
            this.setState({ actionProcessing: false });
          })
          .finally(() => {
            if (success) this.resetAppiumTimeout();
          });

      } else {
        this.setState({
          notification: createNotification(
            "",
            i18n.t("vc.error-params"),
            Severity.ERROR
          ),
        });
      }
    } else {
      this.setState({
        sessionId: sessionId,
      });
      if (appUid !== null && sessionId !== null) {
        this.setState({ actionProcessing: true });
        axios
          .post(APIBaseUrl, {
            sessionid: sessionId,
            type: "WEB",
            VERSION: "1.0",
            deviceName: "deviceName",
            deviceId: appUid,
            platformName: "WEB",
            appPackage: "string",
            appActivity: "string",
            iOSBundleId: "string",
            orgId: "string",
            appiumURL: "string",
            deviceType: "DESIGN",
            extracaps: "string",
            executablePath: "string",
            uwpClassId: "string",
            winappArgs: "string",
            winappWorkingDir: "",
            appconfigurationid: appUid,
          })
          .then((response) => {
            let d = response.data;
            console.log(
              i18n.t("vc.launch", {
                appname:
                  this.platformName === PlatformName.ANDROID
                    ? this.appPackage
                    : this.iOSBundleId,
                device: d.devicename,
              })
            );
            success = true;
            let imageIds = d.extrainfo;
            let iidsArray;
            if (imageIds != null && imageIds != "") {
              var idsStr = imageIds.replace(/\"/g, "");
              iidsArray = idsStr
                .slice(1)
                .substr(0, idsStr.length - 2)
                .split(",");
            }

            this.setState({
              sessionId: d.sessionid,
              snapshotIds: iidsArray,
              recordedSteps: d.recordedsteps,
              actionProcessing: false,
              notification: createNotification(
                "",
                i18n.t("vc.info-launch"),
                Severity.INFO
              ),
            });
            iidsArray.map((id) => {
              const url = APIBaseUrl + sessionId + "/screenshot?imageId=" + id;
              axios
                .get(url)
                .then((response) => {
                  console.log(response.data);
                  let imgObj = {
                    screenshot:
                      "data:image/png;base64," + response.data.screenshot, 
                       id: id
                  };
                  this.setState({
                    snapShotData: [...this.state.snapShotData, imgObj],
                  });
                  let thumbnailImages = this.state.snapShotData;
                  let imageIds = this.state.snapshotIds;
                  let thumbnails =[];
                  if(imageIds.length === thumbnailImages.length) {
                    imageIds.map( (img) => {
                      let imgObj = {};
                      thumbnailImages.map((ele)=> {
                        if(img === ele.id) {
                          imgObj.id = ele.id,
                          imgObj.screenshot = ele.screenshot
                        }
                      });
                      thumbnails.push(imgObj);
                    })
                    this.setState({
                      snapShotData : [],
                      snapShotData : thumbnails
                    })
                  }
                })
                .catch(() => {});
            });
          })
          .catch((error) => {
            this.getDeviceDisplayName(this.deviceName);
            let r = error.response;
            if (r && r.status === 409 && r.data) {
              success = true;
              this.restoreSession(sessionId, r.data);
            } else {
              let name =
                this.platformName === PlatformName.ANDROID
                  ? this.appPackage
                  : this.iOSBundleId;
              this.setState({
                notification: createNotification(
                  "",
                  i18n.t("vc.error-launch", { name }),
                  Severity.ERROR
                ),
              });
            }
            this.setState({ actionProcessing: false });
          })
          .finally(() => {
            if (success) this.resetAppiumTimeout();
          });

        this.handleCloseEvent(sessionId);
      }
    }
  }

  restoreSession(sessionId, data) {
    this.setState({
      sessionId: sessionId,
      notification: createNotification("", i18n.t("vc.restore"), Severity.INFO),
      recordedSteps: data.recordedsteps,
    });
  }

  handleCloseEvent(sessionId) {
    this.closeListener = () => {
      closeSession(sessionId, HWAction.CLOSE.id);
    };
    window.addEventListener("unload", this.closeListener, false);
    window.addEventListener(
      "focus",
      () => {
        window.clearTimeout(this.statusIntervalId);
      },
      false
    );

    window.addEventListener(
      "blur",
      () => {
        this.statusIntervalId = window.setInterval(() => {
          const url = APIBaseUrl + sessionId;
          axios
            .get(url)
            .then((response) => {
              if (
                (this.state.initialised ||
                  isNotificationError(this.state.notification)) &&
                response.data.status !== "running"
              ) {
                window.close();
              }
            })
            .catch(() => {});
        }, 3000);
      },
      false
    );
  }

  resetAppiumTimeout() {
    const appiumTimeout = 1800; // 30 minutes
    const appiumCountdown = 300; // 5 minutes

    window.clearTimeout(this.appiumTimeoutId);
    window.clearTimeout(this.appiumIntervalId);
    this.appiumTimeoutId = window.setTimeout(() => {
      let timeLeft = appiumCountdown;
      this.appiumIntervalId = window.setInterval(() => {
        if (timeLeft >= 0) {
          const minutes = ("0" + Math.floor(timeLeft / 60)).slice(-2);
          const seconds = ("0" + (timeLeft - minutes * 60)).slice(-2);
          this.setState({
            notification: createNotification(
              "",
              i18n.t("vc.appium-timeout", {
                time: minutes + ":" + seconds,
              }),
              Severity.INFO,
              false
            ),
          });
        } else {
          this.setState({ notification: {} });
          window.clearTimeout(this.appiumIntervalId);
          closeSession(this.state.sessionId);
          this.resetState();
          this.setState({
            notification: createNotification(
              "",
              i18n.t("vc.appium-timeout-closed"),
              Severity.INFO
            ),
          });
        }
        timeLeft--;
      }, 1000);
    }, (appiumTimeout - appiumCountdown) * 1000);
  }

  onIncomingScreenshot = (orientation) => {
    if (!this.state.initialised) {
      this.setState({ initialised: true, recordedSteps: null });
    }
    if (this.state.orientation !== orientation) {
      this.setState({ orientation });
    }

    window.clearTimeout(this.screenlockTimeoutId);
    this.setState({ viewerVisibility: true });
  };

  onInitActionPerformed = (actionName) => {
    this.setState({
      actionProcessing: true,
      recordedSteps: null,
      notification: {},
    });

    if (
      this.platformName === PlatformName.IOS &&
      actionName === HWAction.SCREENLOCK.id
    ) {
      // TP-68891: Screen is not refreshed after screen lock on iOS
      this.screenlockTimeoutId = window.setTimeout(() => {
        this.setState({ viewerVisibility: false });
      }, 2000);
    }

    this.resetAppiumTimeout();
  };

  resetState() {
    this.setState({ sessionId: null, initialised: false, sessionClosed: true });
  }

  onActionPerformed = (actionName, recordedSteps, notification) => {
    if (notification) {
      this.setState({ notification });
      if (isNotificationError(notification)) {
        closeSession(this.state.sessionId);
        this.resetState();
      }
    }

    if (!recordedSteps) recordedSteps = null;

    if (actionName === HWAction.CLOSE.id) {
      this.setState({
        notification: createNotification(
          "",
          i18n.t("vc.close", { package: this.appPackage }),
          Severity.INFO
        ),
      });
      this.resetState();
    }
    if (actionName === HWAction.REFRESH.id) {
      this.setState({ refreshScreenshot: !this.state.refreshScreenshot });
    }

    this.setState({
      actionProcessed: !this.state.actionProcessed,
      recordedSteps: recordedSteps,
      actionProcessing: false,
    });

    this.resetAppiumTimeout();
  };

  onNotification = (notification) => {
    this.setState({ notification });
  };

  onTestStepsMinimize = (minimized) => {};

  getDeviceDisplayName(deviceName) {
    var match = /^(.*?)(:.*)/.exec(deviceName);
    if (match && match.length > 2) {
      const type = DeviceType[match[1].toUpperCase()];
      if (type) {
        deviceName = i18n.t("vc.type." + type) + match[2];
      }
    }
    return deviceName;
  }

  setProcessTestStep = (value) => {
    this.setState({
      processTestStep : value
    })
  }

  onKeyPressForAccessible = (e) => {
    if(e.key === "Enter"){
      console.log(e);
      e.target.click();
    }
  }

  render() {
    let viewerClass =
      "vc-viewer" + (this.state.sessionClosed ? " vc-viewer-closed" : "");
    return (
      <main className="vc">
        <header>
          <span className="vc-logo" />
          <span className="logo-text">{i18n.t("vc.logo")}</span>
          <Feedback />
        </header>
        <div className="vc-container">
          <DeviceToolBar
            sessionId={this.state.sessionId}
            platformName={this.platformName}
            deviceName={this.deviceName}
            orientation={this.state.orientation}
            closeListener={this.closeListener}
            onInitAction={this.onInitActionPerformed}
            onAction={this.onActionPerformed}
            hasDesign={this.state.hasDesign}
            onKeyPressForAccessible = {this.onKeyPressForAccessible}
          />
          <div className="vc-content">
            {this.hasDesign === "false" && (
              <Notifier
                notification={this.state.notification}
                onClose={this.onNotification}
              />
            )}

            <>
              {this.state.initialised && (
                <h1 className="vc-title">
                  {this.getDeviceDisplayName(this.deviceName)}
                </h1>
              )}
              <div className={viewerClass}>
                <DeviceViewer
                  sessionId={this.state.sessionId}
                  platformName={this.platformName}
                  visibility={this.state.viewerVisibility}
                  orientation={this.state.orientation}
                  actionProcessed={this.state.actionProcessed}
                  actionProcessing={this.state.actionProcessing}
                  refreshScreenshot={this.state.refreshScreenshot}
                  onInitAction={this.onInitActionPerformed}
                  onAction={this.onActionPerformed}
                  onIncomingScreenshot={this.onIncomingScreenshot}
                  onNotification={this.onNotification}
                  hasDesign={this.state.hasDesign}
                  snapshotIds={this.state.snapshotIds}
                  snapShotData={this.state.snapShotData}
                  processTestStep = {this.state.processTestStep}
                  setProcessTestStep = {this.setProcessTestStep}
                />
              </div>
            </>
          </div>

          <DeviceTestSteps
            sessionId={this.state.sessionId}
            recordedSteps={this.state.recordedSteps}
            actionProcessing={this.state.actionProcessing}
            sessionClosed={this.state.sessionClosed}
            onMinimize={this.onTestStepsMinimize}
            hasDesign={this.state.hasDesign}
            processTestStep = {this.state.processTestStep}
            onKeyPressForAccessible = {this.onKeyPressForAccessible}
          />
        </div>
      </main>
    );
  }
}

export default VirtualClient;
