import React, { Component } from 'react';
import './Feedback.css';
import { VENDOR } from '../vendor/vars';
import i18n from '../i18n';
import SVG_Feedback from '../resources/icons/feedback.svg';

class Feedback extends Component {
    render() {
        let feedBackUrl = (VENDOR === 'IBM')
         ? 'mailto:devops24@us.ibm.com'
         : 'https://onetest-ideas.hcltechsw.com/?category=6820342368443829403';
        return (
            <a
              className="feedback" 
              href={feedBackUrl}
              rel="noopener noreferrer"
              target="_blank">
                {i18n.t('feedback.title')}
                <img src={SVG_Feedback} alt="" />
            </a>
        );
    }
}

export default Feedback;
